
   .todo-container {
    width: 100%;
    max-width: 400px;
    margin: 20px auto;
    padding: 20px;
    background: rgb(63,13,150);
    background: radial-gradient(circle, rgba(63,13,150,1) 10%, rgba(0,0,0,1) 100%);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: left;
  }
  
  .todo-header {
    font-size: 24px;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .input-container {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .todo-input {
    flex: 1;
    padding: 10px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  .todo-button {
    padding: 10px;
    background-color: #28a745;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .todo-button:hover {
    background-color: #218838;
  }
  
  .todo-filters {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .todo-filters label {
    font-size: 14px;
    color: aliceblue;
  }
  
  .todo-filters select {
    padding: 5px;
    margin-left: 5px;
    border-radius: 5px;
  }
  
  .todo-list {
    list-style: none;
    padding: 0;
  }
  
  .todo-list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background: rgb(150,13,13);
     background: radial-gradient(circle, rgba(150,13,13,1) 10%, rgba(0,0,0,1) 100%);
     color:#ccc;
    box-sizing: border-box;
  }
  
  .todo-list-item.completed .task-text {
    text-decoration: line-through;
  }
  
  .todo-list-item input[type='checkbox'] {
    margin-right: 10px;
  }
  
  .task-text {
    flex: 1;
  }
  
  .remove-button {
    background-color: #dc3545;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    padding: 5px 10px;
    font-size: 14px;
  }
  
  .remove-button:hover {
    background-color: #c82333;
  }
  
  @media (max-width: 600px) {
    .todo-container {
      padding: 0px;
      overflow: hidden;
    }
  
    .todo-header {
      font-size: 20px;
    }
  
    .input-container {
      flex-direction: column;
      align-items: stretch;
      overflow: hidden;
    }
  
    .todo-input {
      width: 100%;
      margin-right: 0;
      margin-bottom: 10px;
    }
  
    .todo-button {
      width: 50%;
      margin-left: 25%;
    }
  
    .todo-filters {
      flex-direction: column;
      align-items: stretch;
    }
  
    .todo-filters label {
      margin-bottom: 10px;
    }
  
    .todo-filters select {
      width: 100%;
      margin-left: 0;
    }
  
    .remove-button {
      align-self: flex-end;
    }
  }
  
  
  